<h3>Picker 时间选择</h3>
<hr>
<h4>使用方式</h4>
<pre v-pre><code v-pre class="language-javascript">
<span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>
<span class="hljs-keyword">import</span> { Picker } <span class="hljs-keyword">from</span> <span class="hljs-string">'vvmui'</span>

Vue.component(Picker)

</code></pre>
<h4>基本用法</h4>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">Picker</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">"options"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">options</span>: [
        { <span class="hljs-attr">label</span>: <span class="hljs-string">"选项一"</span> },
        { <span class="hljs-attr">label</span>: <span class="hljs-string">"选项二"</span> },
        { <span class="hljs-attr">label</span>: <span class="hljs-string">"选项三"</span> },
        { <span class="hljs-attr">label</span>: <span class="hljs-string">"选项四"</span> },
        { <span class="hljs-attr">label</span>: <span class="hljs-string">"选项五"</span> },
        { <span class="hljs-attr">label</span>: <span class="hljs-string">"选项六"</span> },
        { <span class="hljs-attr">label</span>: <span class="hljs-string">"选项七"</span> }
      ]
    };
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h4>联动用法</h4>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">Picker</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">"options"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">options</span>: [
        {
          <span class="hljs-attr">value</span>: <span class="hljs-string">"jiangsu"</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">"浙江"</span>,
          <span class="hljs-attr">children</span>: [
            {
              <span class="hljs-attr">value</span>: <span class="hljs-string">"nanjing"</span>,
              <span class="hljs-attr">label</span>: <span class="hljs-string">"杭州"</span>,
              <span class="hljs-attr">children</span>: [
                {
                  <span class="hljs-attr">value</span>: <span class="hljs-string">"fuzimiao"</span>,
                  <span class="hljs-attr">label</span>: <span class="hljs-string">"西湖"</span>
                }
              ]
            },
            {
              <span class="hljs-attr">value</span>: <span class="hljs-string">"suzhou"</span>,
              <span class="hljs-attr">label</span>: <span class="hljs-string">"宁波"</span>,
              <span class="hljs-attr">children</span>: [
                {
                  <span class="hljs-attr">value</span>: <span class="hljs-string">"zhuozhengyuan"</span>,
                  <span class="hljs-attr">label</span>: <span class="hljs-string">"老庙"</span>
                },
                {
                  <span class="hljs-attr">value</span>: <span class="hljs-string">"shizilin"</span>,
                  <span class="hljs-attr">label</span>: <span class="hljs-string">"老街"</span>
                }
              ]
            }
          ]
        },
        {
          <span class="hljs-attr">value</span>: <span class="hljs-string">"jiangsu"</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">"江苏"</span>,
          <span class="hljs-attr">children</span>: [
            {
              <span class="hljs-attr">value</span>: <span class="hljs-string">"nanjing"</span>,
              <span class="hljs-attr">label</span>: <span class="hljs-string">"南京"</span>,
              <span class="hljs-attr">children</span>: [
                {
                  <span class="hljs-attr">value</span>: <span class="hljs-string">"fuzimiao"</span>,
                  <span class="hljs-attr">label</span>: <span class="hljs-string">"夫子庙"</span>
                }
              ]
            },
            {
              <span class="hljs-attr">value</span>: <span class="hljs-string">"suzhou"</span>,
              <span class="hljs-attr">label</span>: <span class="hljs-string">"苏州"</span>,
              <span class="hljs-attr">children</span>: [
                {
                  <span class="hljs-attr">value</span>: <span class="hljs-string">"zhuozhengyuan"</span>,
                  <span class="hljs-attr">label</span>: <span class="hljs-string">"拙政园"</span>
                },
                {
                  <span class="hljs-attr">value</span>: <span class="hljs-string">"shizilin"</span>,
                  <span class="hljs-attr">label</span>: <span class="hljs-string">"狮子林"</span>
                }
              ]
            }
          ]
        }
      ]
    };
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h4>数据绑定</h4>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">Picker</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">"options"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"model"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">options</span>: [
        {
          <span class="hljs-attr">value</span>: <span class="hljs-string">"zhejiang"</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">"浙江"</span>,
          <span class="hljs-attr">children</span>: [
            {
              <span class="hljs-attr">value</span>: <span class="hljs-string">"hangzhou"</span>,
              <span class="hljs-attr">label</span>: <span class="hljs-string">"杭州"</span>,
              <span class="hljs-attr">children</span>: [
                {
                  <span class="hljs-attr">value</span>: <span class="hljs-string">"xihu"</span>,
                  <span class="hljs-attr">label</span>: <span class="hljs-string">"西湖"</span>
                }
              ]
            },
            {
              <span class="hljs-attr">value</span>: <span class="hljs-string">"ninbo"</span>,
              <span class="hljs-attr">label</span>: <span class="hljs-string">"宁波"</span>,
              <span class="hljs-attr">children</span>: [
                {
                  <span class="hljs-attr">value</span>: <span class="hljs-string">"laomiao"</span>,
                  <span class="hljs-attr">label</span>: <span class="hljs-string">"老庙"</span>
                },
                {
                  <span class="hljs-attr">value</span>: <span class="hljs-string">"laojie"</span>,
                  <span class="hljs-attr">label</span>: <span class="hljs-string">"老街"</span>
                }
              ]
            }
          ]
        },
        {
          <span class="hljs-attr">value</span>: <span class="hljs-string">"jiangsu"</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">"江苏"</span>,
          <span class="hljs-attr">children</span>: [
            {
              <span class="hljs-attr">value</span>: <span class="hljs-string">"nanjing"</span>,
              <span class="hljs-attr">label</span>: <span class="hljs-string">"南京"</span>,
              <span class="hljs-attr">children</span>: [
                {
                  <span class="hljs-attr">value</span>: <span class="hljs-string">"fuzimiao"</span>,
                  <span class="hljs-attr">label</span>: <span class="hljs-string">"夫子庙"</span>
                }
              ]
            },
            {
              <span class="hljs-attr">value</span>: <span class="hljs-string">"suzhou"</span>,
              <span class="hljs-attr">label</span>: <span class="hljs-string">"苏州"</span>,
              <span class="hljs-attr">children</span>: [
                {
                  <span class="hljs-attr">value</span>: <span class="hljs-string">"zhuozhengyuan"</span>,
                  <span class="hljs-attr">label</span>: <span class="hljs-string">"拙政园"</span>
                },
                {
                  <span class="hljs-attr">value</span>: <span class="hljs-string">"shizilin"</span>,
                  <span class="hljs-attr">label</span>: <span class="hljs-string">"狮子林"</span>
                }
              ]
            }
          ]
        }
      ],
      <span class="hljs-attr">model</span>: [<span class="hljs-string">"jiangsu"</span>, <span class="hljs-string">"nanjing"</span>, <span class="hljs-string">"fuzimiao"</span>]
    };
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h4>禁用</h4>
<p>通过<code v-pre>disabled</code>可禁用<code v-pre>Picker</code></p>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">Picker</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">"options"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"model"</span> /&gt;</span>

</code></pre>
<h4>异步级联数据</h4>
<p>当<code v-pre>options</code>为空数组时，<code v-pre>Picker</code>会呈现加载状态</p>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">Picker</span> <span class="hljs-attr">@on-show</span>=<span class="hljs-string">"show"</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">"options"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">options</span>: []
    };
  },
  <span class="hljs-attr">methods</span>: {
    show() {
      <span class="hljs-keyword">const</span> that = <span class="hljs-keyword">this</span>;
      <span class="hljs-keyword">const</span> t = setTimeout(<span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>) </span>{
        clearTimeout(t);
        that.options = [
          {
            <span class="hljs-attr">value</span>: <span class="hljs-string">"zhejiang"</span>,
            <span class="hljs-attr">label</span>: <span class="hljs-string">"浙江"</span>,
            <span class="hljs-attr">children</span>: [
              {
                <span class="hljs-attr">value</span>: <span class="hljs-string">"hangzhou"</span>,
                <span class="hljs-attr">label</span>: <span class="hljs-string">"杭州"</span>,
                <span class="hljs-attr">children</span>: [
                  {
                    <span class="hljs-attr">value</span>: <span class="hljs-string">"xihu"</span>,
                    <span class="hljs-attr">label</span>: <span class="hljs-string">"西湖"</span>
                  }
                ]
              },
              {
                <span class="hljs-attr">value</span>: <span class="hljs-string">"ninbo"</span>,
                <span class="hljs-attr">label</span>: <span class="hljs-string">"宁波"</span>,
                <span class="hljs-attr">children</span>: [
                  {
                    <span class="hljs-attr">value</span>: <span class="hljs-string">"laomiao"</span>,
                    <span class="hljs-attr">label</span>: <span class="hljs-string">"老庙"</span>
                  },
                  {
                    <span class="hljs-attr">value</span>: <span class="hljs-string">"laojie"</span>,
                    <span class="hljs-attr">label</span>: <span class="hljs-string">"老街"</span>
                  }
                ]
              }
            ]
          },
          {
            <span class="hljs-attr">value</span>: <span class="hljs-string">"jiangsu"</span>,
            <span class="hljs-attr">label</span>: <span class="hljs-string">"江苏"</span>,
            <span class="hljs-attr">children</span>: [
              {
                <span class="hljs-attr">value</span>: <span class="hljs-string">"nanjing"</span>,
                <span class="hljs-attr">label</span>: <span class="hljs-string">"南京"</span>,
                <span class="hljs-attr">children</span>: [
                  {
                    <span class="hljs-attr">value</span>: <span class="hljs-string">"fuzimiao"</span>,
                    <span class="hljs-attr">label</span>: <span class="hljs-string">"夫子庙"</span>
                  }
                ]
              },
              {
                <span class="hljs-attr">value</span>: <span class="hljs-string">"suzhou"</span>,
                <span class="hljs-attr">label</span>: <span class="hljs-string">"苏州"</span>,
                <span class="hljs-attr">children</span>: [
                  {
                    <span class="hljs-attr">value</span>: <span class="hljs-string">"zhuozhengyuan"</span>,
                    <span class="hljs-attr">label</span>: <span class="hljs-string">"拙政园"</span>
                  },
                  {
                    <span class="hljs-attr">value</span>: <span class="hljs-string">"shizilin"</span>,
                    <span class="hljs-attr">label</span>: <span class="hljs-string">"狮子林"</span>
                  }
                ]
              }
            ]
          }
        ];
      }, <span class="hljs-number">2000</span>);
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h3>自定义数据解析方式</h3>
<p><code v-pre>label</code>用于展示的字段、<code v-pre>children</code>子集数据字段、<code v-pre>key-value</code>选中时返回的字段</p>
<h4>代码示例</h4>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">Picker</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">"options"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"name"</span> <span class="hljs-attr">children</span>=<span class="hljs-string">"component"</span> <span class="hljs-attr">key-value</span>=<span class="hljs-string">"name"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">value</span>: [],
      <span class="hljs-attr">options</span>: [
        {
          <span class="hljs-attr">name</span>: <span class="hljs-string">"浙江"</span>,
          <span class="hljs-attr">component</span>: [
            {
              <span class="hljs-attr">name</span>: <span class="hljs-string">"杭州"</span>,
              <span class="hljs-attr">component</span>: [
                {
                  <span class="hljs-attr">value</span>: <span class="hljs-string">"xihu"</span>,
                  <span class="hljs-attr">name</span>: <span class="hljs-string">"西湖"</span>
                }
              ]
            },
            {
              <span class="hljs-attr">name</span>: <span class="hljs-string">"宁波"</span>,
              <span class="hljs-attr">component</span>: [
                {
                  <span class="hljs-attr">name</span>: <span class="hljs-string">"老庙"</span>
                },
                {
                  <span class="hljs-attr">name</span>: <span class="hljs-string">"老街"</span>
                }
              ]
            }
          ]
        },
        {
          <span class="hljs-attr">name</span>: <span class="hljs-string">"江苏"</span>,
          <span class="hljs-attr">component</span>: [
            {
              <span class="hljs-attr">name</span>: <span class="hljs-string">"南京"</span>,
              <span class="hljs-attr">component</span>: [
                {
                  <span class="hljs-attr">name</span>: <span class="hljs-string">"夫子庙"</span>
                }
              ]
            },
            {
              <span class="hljs-attr">name</span>: <span class="hljs-string">"苏州"</span>,
              <span class="hljs-attr">component</span>: [
                {
                  <span class="hljs-attr">name</span>: <span class="hljs-string">"拙政园"</span>
                },
                {
                  <span class="hljs-attr">name</span>: <span class="hljs-string">"狮子林"</span>
                }
              ]
            }
          ]
        }
      ]
    };
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h3>API</h3>
<h5>Picker props</h5>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>placeholder</td>
<td>占位内容</td>
<td>String</td>
<td>无</td>
<td>请选择</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>cancel-text</td>
<td>取消文本</td>
<td>String</td>
<td>无</td>
<td>取消</td>
</tr>
<tr>
<td>confirm-text</td>
<td>确认文本</td>
<td>String</td>
<td>无</td>
<td>确认</td>
</tr>
<tr>
<td>key-value</td>
<td>选中数据返回的字段</td>
<td>String</td>
<td>无</td>
<td>value</td>
</tr>
<tr>
<td>label</td>
<td>展示的字段</td>
<td>String</td>
<td>无</td>
<td>label</td>
</tr>
<tr>
<td>v-model</td>
<td>绑定的值</td>
<td>String</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>height</td>
<td>Picker每一项的高度</td>
<td>Number</td>
<td>无</td>
<td>38</td>
</tr>
<tr>
<td>children</td>
<td>读取<code v-pre>options</code>子集的字段</td>
<td>String</td>
<td>无</td>
<td>children</td>
</tr>
</tbody>
</table>
<h5>Picker Events</h5>
<table>
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-cancel</td>
<td>点击取消关闭时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-change</td>
<td>滑动选择时触发</td>
<td>当前选中值的集合和所有选中的下标集合，<code v-pre>(value,item) =&gt; {}</code></td>
</tr>
<tr>
<td>on-show</td>
<td>弹出选择框时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-confirm</td>
<td>点击确认时触发</td>
<td>当前选中值的集合</td>
</tr>
</tbody>
</table>
<h5>更新日志</h5>
<table>
<thead>
<tr>
<th style="text-align:center">版本</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">内容</th>
</tr>
</thead>
<tbody></tbody>
</table>
</section></template>

<script>
export default {
  data() {
    return {
      options: [
        { label: "选项一" },
        { label: "选项二" },
        { label: "选项三" },
        { label: "选项四" },
        { label: "选项五" },
        { label: "选项六" },
        { label: "选项七" }
      ]
    };
  }
};
</script>


